.information {
  display: flex;
  justify-content: center;

  &__wrapper {
    display: flex;
    max-width: 1440px;
  }

  &__content {
    /* grid-column-start: 3;
  grid-column-end: span 3; */
    padding-left: 106px;
    padding-right: 168px;
  }

  &__image {
    max-width: 480px;
    width: 100%;
  }

  &__text--top {
    font-size: 16px;
    line-height: 12px;
    letter-spacing: 0.06em;
    color: #22356f;
    padding-top: 100px;
  }

  &__text--center {
    font-size: 36px;
    line-height: 53px;
    letter-spacing: -0.01em;
    color: #22356f;
    display: block;
    max-width: 676px;
    width: 100%;
    margin-top: 224px;
  }
  &__text--bottom {
    font-size: 16px;
    line-height: 23px;
    letter-spacing: 0.1em;
    color: #fab63e;
    margin-top: 150px;
  }

  &__text--lightBlue {
    color: #62d0df;
  }
}
