.chapter {
  background-color: #22356f;
  display: flex;
  justify-content: center;
  &__wrapper {
    display: flex;
    max-width: 1440px;
  }

  &__wrapper-text {
    padding-left: 180px;
  }

  &__text--top {
    font-size: 16px;
    line-height: 12px;
    letter-spacing: 0.06em;
    color: #ffffff;
    padding-top: 100px;
  }

  &__title {
    font-size: 64px;
    line-height: 94px;
    letter-spacing: -0.01em;
    color: #ffffff;
    margin-top: 230px;
  }

  &__text--center {
    font-size: 36px;
    line-height: 53px;
    letter-spacing: -0.005em;
    color: #ffffff;
    display: block;
    max-width: 580px;
    width: 100%;
    margin-right: 200px;
  }

  &__text--bottom {
    font-size: 16px;
    line-height: 23px;
    letter-spacing: 0.1em;
    color: #62d0df;
    margin-top: 50px;
  }

  &__img {
    max-width: 480px;
    width: 100%;
  }
}
