.therapy {
  &__wrapper {
    display: grid;
    grid-template-columns: repeat(3, 480px);
    grid-template-rows: repeat(2, 240px);
    justify-items: left;
    justify-content: center;
  }

  &__block {
    padding: 50px 70px;
  }
  &__subtitle {
    font-size: 28px;
    line-height: 33px;
    letter-spacing: -0.03em;
    color: #22356f;
    margin-bottom: 28px;
  }
  &__text {
    font-size: 16px;
    line-height: 25px;
    letter-spacing: -0.03em;
    color: rgba(34, 53, 111, 0.5);
    max-width: 298px;
  }
}
