@media all and (max-width: 1225px) {
  .main__image,
  .main__rect,
  .main::after {
    display: none;
  }
  .main,
  .header__button {
    padding-left: 14px;
    padding-right: 14px;
  }

  .header {
    padding-left: 22px;
    padding-right: 22px;
  }

  .main__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

@media all and (max-width: 768px) {
  .header {
    background-color: #f4f9e2;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    transform: translateX(0);
    -webkit-transform: translateX(0);
    -moz-transform: translateX(0);
    -ms-transform: translateX(0);
    -o-transform: translateX(0);
  }
  .header__btn {
    display: none;
  }
  .header__logo {
    font-size: 40px;
  }

  .header__subtitle--center {
    display: none;
  }
  .header__burger {
    display: block;
    width: 50px;
    z-index: 99;
  }
  .header__nav {
    padding-top: 150px;
    max-width: initial;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #f4f9e2;
    transform: translateX(-100%);
    transition: transform 0.5s ease;
    -webkit-transform: translateX(-100%);
    -moz-transform: translateX(-100%);
    -ms-transform: translateX(-100%);
    -o-transform: translateX(-100%);
    -webkit-transition: transform 0.5s ease;
    -moz-transition: transform 0.5s ease;
    -ms-transition: transform 0.5s ease;
    -o-transition: transform 0.5s ease;
  }
  .header__list {
    flex-direction: column;
  }
  .header__item {
    margin-bottom: 30px;
  }
  .header__item:last-child {
    margin-bottom: 0;
  }
  .header__link {
    font-size: 36px;
    line-height: 26px;
    color: #22356f;
    text-decoration: none;
  }
  .header__nav.active {
    transform: translateX(0);
    -webkit-transform: translateX(0);
    -moz-transform: translateX(0);
    -ms-transform: translateX(0);
    -o-transform: translateX(0);
  }
}

@media all and (max-width: 376px) {
  .main__wrapper {
    margin-top: 210px;
  }
}
.header__text,
.description__text,
.information__text--center {
  font-size: 20px;
}

.description {
  background-color: #62d0df;
  display: flex;
  justify-content: space-between;
}
.description__wrapper {
  max-width: 1080px;
}

.description__image {
  max-width: 525px;
  width: 100%;
}

.description__title {
  font-size: 36px;
  line-height: 27px;
  letter-spacing: -0.006em;
  color: #ffffff;
  padding-top: 150px;
  margin-bottom: 75px;
}

.description__text {
  font-size: 24px;
  line-height: 35px;
  color: #ffffff;
  max-width: 555px;
  width: 100%;
}

@media all and (max-width: 1156px) {
  .description {
    flex-wrap: wrap;
    justify-content: center;
    padding-left: 14px;
    padding-right: 14px;
  }
  .description__content {
    margin-bottom: 20px;
  }
  .description__title {
    padding-top: 80px;
    margin-bottom: 50px;
  }
}

@media all and (max-width: 600px) {
  .header__button--left,
  .header__button--right {
    width: 130px;
  }
}

@media all and (max-width: 1438px) {
  .help__block {
    justify-content: start;
  }
}

@media all and (max-width: 1180px) {
  .help__text--left {
    display: none;
  }
  .help__text--center::after {
    display: initial;
  }

  .help__text--center {
    padding-left: 14px;
    padding-right: 14px;
    font-size: 20px;
  }
}

@media all and (max-width: 768px) {
  .help__block {
    margin-bottom: 70px;
  }
}

@media all and (max-width: 1438px) {
  .complex__wrapper {
    grid-template-columns: repeat(6, 480px);
    grid-template-rows: repeat(1, 480px);
    overflow: auto;
    justify-content: start;
  }
}

@media all and (max-width: 475px) {
  .therapy__subtitle {
    min-height: 66px;
  }
  .complex__wrapper {
    grid-template-columns: repeat(6, 400px);
    grid-template-rows: repeat(1, 400px);
  }
}

@media all and (max-width: 1338px) {
  .therapy__wrapper {
    grid-template-columns: repeat(2, 480px);
    grid-row-gap: 10px;
  }
}

@media all and (max-width: 900px) {
  .therapy__wrapper {
    grid-template-columns: repeat(2, 1fr);
    padding-left: 14px;
    padding-right: 14px;
  }
}
@media all and (max-width: 620px) {
  .therapy__block {
    padding: 10px;
  }
}

@media all and (max-width: 1390px) {
  .information__text--center {
    margin-top: 100px;
    font-size: 24px;
  }
  .information__text--bottom {
    margin-top: 50px;
  }
}

@media all and (max-width: 1036px) {
  .information__text--center {
    margin-top: 50px;
    font-size: 22px;
    line-height: 36px;
  }
  .information__text--bottom {
    margin-top: 30px;
    padding-bottom: 10px;
  }
  .information__text--top {
    padding-top: 50px;
  }
}
@media all and (max-width: 880px) {
  .information__content {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media all and (max-width: 588px) {
  .information__image {
    display: none;
  }
}

@media all and (max-width: 1336px) {
  .chapter__title {
    margin-top: 100px;
  }
}
@media all and (max-width: 1170px) {
  .chapter__title {
    margin-top: 80px;
    font-size: 46px;
  }
  .chapter__text--center {
    font-size: 24px;
  }
  .chapter__wrapper-text {
    padding-right: 20px;
    padding-left: 50px;
  }
  .chapter__text--bottom {
    padding-bottom: 20px;
  }
}
@media all and (max-width: 910px) {
  .chapter__text--center {
    font-size: 20px;
    line-height: 36px;
    margin-right: 0;
  }
  .chapter__text--top {
    padding-top: 50px;
  }
  .chapter__title {
    margin-top: 20px;
    font-size: 36px;
  }

  .chapter {
    justify-content: flex-end;
  }
}
@media all and (max-width: 730px) {
  .chapter__img {
    display: none;
  }
  .chapter__wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media all and (max-width: 376px) {
  .chapter__wrapper-text {
    padding-left: 0;
    padding-right: 0;
  }
}

@media all and (min-width: 1441px) {
  .footer__wrapper {
    justify-content: center;
  }
}

@media all and (max-width: 1438px) {
  .footer__block {
    padding-bottom: 20px;
  }
  .footer__cell {
    padding-left: 30px;
  }
}

@media all and (max-width: 1244px) {
  .footer__wrapper {
    padding-right: 14px;
    justify-content: center;
  }
  .footer__cell {
    padding-top: 50px;
  }
}
@media all and (max-width: 1025px) {
  .footer__wrapper {
    flex-wrap: wrap;
    padding: 0;
  }
  .footer__title {
    margin-bottom: 20px;
  }
}

@media all and (max-width: 768px) {
  .footer__wrapper {
    margin: 0;
  }
  .footer__block {
    padding-left: 14px;
    padding-right: 14px;
    padding-bottom: 20px;
    flex-wrap: wrap;
  }
  .footer__img {
    width: 100%;
    max-width: 100%;
  }
  .footer__cell:not(:last-child) {
    margin-right: 12px;
  }
  .footer__title {
    line-height: 18px;
  }

  .footer__text:nth-child(2) {
    margin-bottom: 10px;
  }

  .footer__bottom {
    text-align: center;
  }

  .footer__small {
    margin-left: 0;
  }
  .footer__cell {
    padding-top: 30px;
  }
}
