.header {
  position: absolute;
  z-index: 9;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  max-width: 1080px;
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  -webkit-transform: translateX(-50%);
  -moz-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  -o-transform: translateX(-50%);
  flex-wrap: wrap;

  &__logo,
  &__subtitle {
    font-size: 24px;
    line-height: 27px;
    color: #62d0df;
  }

  &__nav {
    display: flex;
    align-items: center;
    max-width: 428px;
    justify-content: space-between;
  }

  &__list {
    display: flex;
    max-width: 233px;
    justify-content: center;
    text-decoration: none;
    font-size: 14px;
    line-height: 16px;
    color: #22356f;
    list-style: none;
  }

  &__item {
    position: relative;
    padding: 0 6px;
    margin-right: 20px;
    &.active::after {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      bottom: -10px;
      height: 2px;
      background-color: silver;
    }

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    text-decoration: none;
    font-size: 14px;
  }
  &__btn {
    background: #62d0df;
    border-radius: 50px;
    color: #ffffff;
    display: flex;
    align-items: center;
    width: 150px;
    height: 50px;
    justify-content: center;
    border: none;
  }

  &__title {
    font-size: 48px;
    line-height: 53px;
    letter-spacing: -0.035em;
    color: #22356f;
    max-width: 484px;
    width: 100%;
  }

  &__text {
    font-size: 24px;
    line-height: 42px;
    color: #22356f;
    max-width: 470px;
    width: 100%;
    padding-top: 20px;
  }

  &__button--left,
  &__button--right {
    width: 150px;
    height: 50px;
    line-height: 23px;
    background: #f9f5e2;
    border: none;
    font-family: Larsseit;
    font-size: 16px;
    color: #fab63e;
    margin-top: 70px;
  }
  &__button {
    display: flex;
  }
  &__button--left:hover,
  &__button--right:hover {
    background: #f9b640;
    color: #ffffff;
  }
  &__burger {
    display: none;
  }
}
