.main {
  position: relative;
  padding-bottom: 80px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f4f9e2;

  &::after {
    content: "";
    background-color: white;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    width: 320px;
  }

  &__wrapper {
    max-width: 1080px;
    width: 100%;
    margin-top: 240px;
  }

  &__image {
    position: absolute;
    z-index: 2;
    top: 0;
    right: 0;
    max-width: 806px;
    width: 100%;
  }

  &__rect {
    position: absolute;
    z-index: 1;
    top: 0;
    right: 0;
    max-width: 770px;
    width: 100%;
  }
}
