.complex {
  &__wrapper {
    display: grid;
    grid-template-columns: repeat(3, 480px);
    grid-template-rows: repeat(2, 480px);
    justify-items: center;
    margin-bottom: 22px;
    justify-content: center;
  }

  &__text {
    font-size: 32px;
    line-height: 34px;
    letter-spacing: -0.006em;
    color: #ffffff;
    margin-top: 126px;
    margin-left: 105px;
  }

  &__cell {
    width: 100%;
    /* text-align: center; */
    position: relative;
  }

  &__text::before {
    content: "";
    position: absolute;
    top: 100px;
    left: 70px;
    right: 70px;
    height: 2px;
    background-color: #fff;
  }

  &__cell--navyBlue {
    background-color: #22356f;
  }
  &__cell--blue {
    background-color: #0052c1;
    width: 100%;
  }
  &__cell--light {
    background-image: url(../img/joanna-kosinska-340748.png);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }
  &__cell--lightBlue {
    background-color: #62d0df;
  }
  &__cell--grey {
    background-color: #dee1ff;
  }
  &__cell--violet {
    background-color: #8f00ff;
  }

  &__text::after {
    content: "";
    position: absolute;
    display: block;
    top: 134px;
    left: 70px;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: #e9b730;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
  }
}
