.footer {
  &__img {
    max-width: 570px;
    width: 100%;
  }

  &__block {
    max-width: 852px;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
  }

  &__cell {
    max-width: 200px;
    width: 100%;
    padding-top: 100px;
  }

  &__title {
    font-size: 14px;
    line-height: 11px;
    letter-spacing: -0.006em;
    color: #22356f;
    margin-bottom: 50px;
  }

  &__text {
    font-size: 14px;
    line-height: 23px;
    letter-spacing: -0.006em;
    color: rgba(34, 53, 111, 0.5);
    margin-bottom: 30px;
  }

  &__tel,
  &__mail {
    text-decoration: none;
  }
  &__tel {
    color: rgba(34, 53, 111, 0.5);
  }
  &__mail {
    color: rgb(55, 116, 247);
  }

  &__bottom {
    background-color: #62d0df;
    padding: 32px;
  }

  &__small {
    margin-left: 178px;
    color: #ffffff;
    position: relative;
  }

  &__small::after {
    content: "";
    position: absolute;
    background: url(../img/Â©.png);
    width: 9px;
    height: 9px;
    right: 151px;
    top: 2px;
  }
  &__wrapper {
    display: flex;
  }
}
