.help {
  &__content {
    padding-top: 150px;
    position: relative;
    display: flex;
    justify-content: center;
  }

  &__text--left {
    font-size: 16px;
    line-height: 23px;
    letter-spacing: 0.06em;
    color: #22356f;
    position: absolute;
    padding-left: 100px;
    margin-top: -260px;
    margin-right: 1176px;
  }

  &__text--center {
    font-size: 24px;
    line-height: 34px;
    letter-spacing: 0.005em;
    color: #22356f;
    max-width: 590px;
    width: 100%;
  }

  &__text {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-bottom: 24px;
    padding-top: 150px;
  }

  &__text--center {
    position: relative;
  }
  &__text--center::after {
    content: "ABOUT US";
    position: absolute;
    display: none;
    font-size: 16px;
    line-height: 23px;
    letter-spacing: 0.06em;
    color: #22356f;
    top: -50px;
    left: 10px;
  }

  &__text--bottom {
    color: rgba(34, 53, 111, 0.5);
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 0.06em;
    padding-top: 76px;
  }

  &__block {
    display: grid;
    grid-template-columns: repeat(4, 360px);
    height: 360px;
    margin-bottom: 120px;
    justify-content: center;
    overflow: auto;
  }

  &__block--pink {
    background-color: #ffe2de;
    width: 100%;
    text-align: center;
  }

  &__block--green {
    background-color: #c4e769;
    width: 100%;
    text-align: center;
  }

  &__block--lightBlue {
    background-color: #62d0df;
    width: 100%;
    text-align: center;
  }

  &__block--Blue {
    background-color: #0052c1;
    width: 100%;
    text-align: center;
  }

  &__word {
    position: relative;
    font-size: 32px;
    line-height: 34px;
    letter-spacing: -0.006em;
    color: #ffffff;
    padding-top: 120px;
  }

  &__word::after {
    content: "";
    position: absolute;
    top: 100px;
    left: 52px;
    right: 52px;
    height: 2px;
    background-color: #fff;
  }

  &__word::before {
    content: "";
    position: absolute;
    top: 132px;
    left: 58px;
    width: 9px;
    height: 9px;
    border-radius: 50%;
    background-color: #e9b730;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
  }
}
